p {
  color: brown;
}



.section_support {
  width: 335px;
  height: 352px;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
}

.slider_line {
  width: 300px;
  height: 200px;
  transition: all 1s;
  position: relative;
  top: 0;
}

.slider {
  overflow: hidden;
}

.ol_support {
  counter-reset: item;
  color: #fff;
  letter-spacing: -4%;
  flex-direction: column;
  gap: 20px;
  margin-top: 40px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  list-style-type: none;
  display: flex;
}

li:before {
  content: "0"counter(item);
  counter-increment: item;
  margin-right: 16px;
}

.header_support {
  letter-spacing: -4%;
  color: #fff;
  padding-top: 24px;
  padding-left: 40px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14;
}

.down-scroll {
  padding-left: 152px;
  position: fixed;
}



















* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --container-width: 100%;
  --background-color: #f6f6f6;
  --header-background-color: #fff;
  --font-color-dark: #111;
  --font-color-selected: #4f2ee8;
  --author-category-booklist-font-color: #b4afaf;
  --support-ukraine-font-color: #fff;
  --categories-font-color: rgba(17, 17, 17, .6);
  --header-border-color: #111;
  --shopping-list-book-card-border-color: rgba(79, 46, 232, .4);
  --categories-scroll-color: #e0e0e0;
  --support-ukraine-modal-menu-background: radial-gradient( 189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100% );
}

body {
  font-family: DM Sans, sans-serif;
}

.container {
  width: var(--container-width);
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 375px) {
  :root {
    --container-width: 375px;
  }

  @media (min-width: 768px) {
    :root {
      --container-width: 768px;
    }

    .container {
      padding-left: 32px;
      padding-right: 32px;
    }
  }
}

@media (min-width: 1440px) {
  :root {
    --container-width: 1440px;
  }

  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}

